import {AfterViewInit, Component, HostListener, OnInit} from '@angular/core';
import * as $ from 'jquery';
declare var require: any;

var $ = require('jquery');
window["jQuery"] = window['$'] = $;
require('velocity-animate');
delete window['jQuery'];
delete window['$'];

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, AfterViewInit {

  public items: any[];
  private sliderFinalWidth = 400;
  private maxQuickWidth = 900;
  @HostListener('document:keydown', ['$event'])
  handleEvent(event) {
    if (event.keyCode === 27) {
      this.closeQuickView(this.sliderFinalWidth, this.maxQuickWidth);
    }
  }

  constructor() {
    this.items = [
      {
        img_path: 'assets/img/city.jpg',
        product_title: 'Продукт 1',
        description: 'Описание продукта 1'
      },
      {
        img_path: 'assets/img/item-1.jpg',
        product_title: 'Продукт 2',
        description: 'Описание продукта 2'
      },
      {
        img_path: 'assets/img/item-1.jpg',
        product_title: 'Продукт 3',
        description: 'Описание продукта 3'
      },
      {
        img_path: 'assets/img/item-1.jpg',
        product_title: 'Продукт 4',
        description: 'Описание продукта 4'
      }
    ];
  }

  ngOnInit() {

  }

  ngAfterViewInit() {
    $('body').on('click', (event) => {
      if ($(event.target).is('.cd-close') || $(event.target).is('body.overlay-layer')) {
        this.closeQuickView(this.sliderFinalWidth, this.maxQuickWidth);
      }
    });

    $('.cd-trigger').on('click', (event) => {
      let selectedImage = $(event.currentTarget).parent('.cd-item').children('img'),
        selectedImageUrl = selectedImage.attr('src');
      $('body').addClass('overlay-layer');
      this.animateQuickView(selectedImage, this.sliderFinalWidth, this.maxQuickWidth, 'open');
    });
  }

  animateQuickView(image, finalWidth, maxQuickWidth, animationType) {
    let parentListItem = image.parent('.cd-item');
    let topSelected = image.offset().top - $(window).scrollTop(),
      leftSelected = image.offset().left,
      widthSelected = image.width(),
      heightSelected = image.height(),
      windowWidth = $(window).width(),
      windowHeight = $(window).height(),
      finalLeft = (windowWidth - finalWidth) / 2,
      finalHeight = finalWidth * heightSelected / widthSelected,
      finalTop = (windowHeight - finalHeight) / 2,
      quickViewWidth = ( windowWidth * .8 < maxQuickWidth ) ? windowWidth * .8 : maxQuickWidth ,
      quickViewLeft = (windowWidth - quickViewWidth) / 2;

    if (animationType === 'open') {
      parentListItem.addClass('empty-box');
      $('.cd-quick-view').css({
        'top': topSelected,
        'left': leftSelected,
        'width': widthSelected,
      }).velocity({
        'top': finalTop + 'px',
        'left': finalLeft + 'px',
        'width': finalWidth + 'px',
      }, 1000, [ 400, 20 ], function() {
        $('.cd-quick-view').addClass('animate-width').velocity({
          'left': quickViewLeft + 'px',
          'width': quickViewWidth + 'px',
        }, 300, 'ease', function() {
          $('.cd-quick-view').addClass('add-content');
        });
      }).addClass('is-visible');
    } else {
      $('.cd-quick-view').removeClass('add-content').velocity({
        'top': finalTop + 'px',
        'left': finalLeft + 'px',
        'width': finalWidth + 'px',
      }, 300, 'ease', function() {
        $('body').removeClass('overlay-layer');
        $('.cd-quick-view').removeClass('animate-width').velocity({
          'top': topSelected,
          'left': leftSelected,
          'width': widthSelected,
        }, 500, 'ease', function() {
          $('.cd-quick-view').removeClass('is-visible');
          parentListItem.removeClass('empty-box');
        });
      });
    }
  }

  closeQuickView(finalWidth, maxQuickWidth) {
    let close = $('.cd-close'),
      activeSliderUrl = close.siblings('.cd-slider-wrapper').find('.selected img').attr('src'),
      selectedImage = $('.empty-box').find('img');
    selectedImage.attr('src', activeSliderUrl);
    if ( !$('.cd-quick-view').hasClass('velocity-animating') && $('.cd-quick-view').hasClass('add-content')) {
      selectedImage.attr('src', activeSliderUrl);
      this.animateQuickView(selectedImage, finalWidth, maxQuickWidth, 'close');
    } else {
      this.closeNoAnimation(selectedImage, finalWidth, maxQuickWidth);
    }
  }

  closeNoAnimation(image, finalWidth, maxQuickWidth) {
    let parentListItem = image.parent('.cd-item'),
      topSelected = image.offset().top - $(window).scrollTop(),
      leftSelected = image.offset().left,
      widthSelected = image.width();

    $('body').removeClass('overlay-layer');
    parentListItem.removeClass('empty-box');
    $('.cd-quick-view').velocity('stop').removeClass('add-content animate-width is-visible').css({
      'top': topSelected,
      'left': leftSelected,
      'width': widthSelected,
    });
  }
}



